        </a-menu-item>
        <a-menu-item key="5">
          LAN
        </a-menu-item>
        <a-menu-item key="6">
          
        </a-menu-item>
        <a-menu-item key="7">
          RS232
        </a-menu-item>
        <a-menu-item key="8">
          RS485
        </a-menu-item>
        <a-menu-item key="9">
          IO
        </a-menu-item>
        <a-menu-item key="10">
          
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub2">
        <span slot="title"><a-icon type="setting" /><span></span></span>
        <a-menu-item key="11">
          
        </a-menu-item>
        <a-menu-item key="12">
          
        </a-menu-item>
        <a-menu-item key="13">
          
        </a-menu-item>
        <a-menu-item key=14>
          
        </a-menu-item>
        <a-menu-item key="15">
          
        </a-menu-item>
        <a-menu-item key="16">
          
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub3">
        <span slot="title"><a-icon type="setting" /><span></span></span>
        <a-menu-item key="17">
          
        </a-menu-item>
        <a-menu-item key="18">
          
        </a-menu-item>
      </a-sub-menu>
      </a-menu>
    </a-layout-sider>
    <a-layout>
      <router-view/>
    </a-layout>
  </a-layout>
