<script>
export default {
    methods: {
      onCollapse (collapsed, type) {
        console.log(collapsed, type)
        console.log('onCollapse')
      },
      onBreakpoint (broken) {
        console.log(broken)
        console.log('onBreakpoint')
        this.$router.push({name: 'firstPage'})
      },
      handleClick(e) {
        switch(e.key){
          case("1"):
            this.$router.push({name: 'firstPage'});
            break;
          case("2"):
            this.$router.push({name: 'WANPage'});
            break;
          case("3"):
            this.$router.push({name: '_4GPage'});
            break;
          case("4"):
            this.$router.push({name: 'WIFIPage'});
            break;
          case("5"):
            this.$router.push({name: 'LANPage'});
            break;
          case("6"):
            this.$router.push({name: 'joinRouter'});
            break;
          case("7"):
            this.$router.push({name: 'RS232Page'});
            break;
          case("8"):
            this.$router.push({name: 'RS485Page'});
            break;
          case("9"):
            this.$router.push({name: 'IOPage'});
            break;
          case("10"):
            this.$router.push({name: 'uploadPage'});
            break;
          case("11"):
            this.$router.push({name: 'CNCBase'});
            break;
          case("12"):
            this.$router.push({name: 'CNCFile'});
            break;
          case("13"):
            this.$router.push({name: 'CNCZhou'});
            break;
          case("14"):
            this.$router.push({name: 'CNCZDY'});
            break;
          case("15"):
            this.$router.push({name: 'CNCZDYH'});
            break;
          case("16"):
            this.$router.push({name: 'CNCDJ'});
            break;
          case("17"):
            this.$router.push({name: 'ApplicationPage'});
            break;
          case("18"):
            this.$router.push({name: 'userPage'});
            break;
        }
          
        
        
    },
  }
}
</script>

<style>
.components-layout{
  height: 100vh;
  width: 100%;
}
.components-logo {
  height: 10vh;
  width: 75%;
  background:url(../assets/logo.png);
  margin: 18px;
}
